<template>
    <transition name="move">
        <div class="food" v-show="showFlag" ref="food">
            <div class="food-content">
                <div class="image-header">
                    <img :src="food.image" alt="">
                    <div class="back" @click="hide">
                        <i class="icon-arrow_lift"></i>
                    </div>
                </div>
                <div class="content">
                    <h1 class="title">{{ food.name }}</h1>
                    <div class="detail">
                        <span class="sell-count">月售{{ food.sellCount }}</span>
                        <span class="rating">好评率{{ food.rating }}%</span>
                    </div>
                    <div class="price">
                        <span class="now">￥{{ food.price }}</span><span v-show="food.oldPrice" class="old">￥{{ food.oldPrice }}</span>
                    </div>
                    <div class="cartcontrol-wrapper">
                        <cartcontrol :food="food"></cartcontrol>
                    </div>
                    <transition name="fade">
                        <div class="buy" v-show="!food.nums|| food.nums === 0" @click.stop.prevent="addFirst">加入购物车
                        </div>
                    </transition>
                </div>
                <split v-show="food.info"></split>
                <div class="info" v-show="food.info">
                    <h1 class="title">商品信息</h1>
                    <p class="text">{{ food.info }}</p>
                </div>
                <!--<split></split>-->
                <!--<div class="rating" v-if="0">-->
                <!--<h1 class="title">商品评价</h1>-->
                <!--<ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc"-->
                <!--:ratings="food.ratings"></ratingselect>-->
                <!--<div class="rating-wrapper">-->
                <!--<ul v-show="food.ratings && food.ratings.length">-->
                <!--<li v-show="needShow(rating.rateType, rating.text)" v-for="rating in food.ratings"-->
                <!--class="rating-item border-1px">-->
                <!--<div class="user">-->
                <!--<span class="name">{{ rating.username }}</span>-->
                <!--<img :src="rating.avatar" class="avatar" width="12" height="12">-->
                <!--</div>-->
                <!--<div class="time">{{ rating.rateTime | formatDate }}</div>-->
                <!--<div class="text">-->
                <!--<span :class="{'icon-thumb_up': rating.rateType === 0, 'icon-thumb_down': rating.rateType === 1}"></span>{{-->
                <!--rating.text }}-->
                <!--</div>-->
                <!--</li>-->
                <!--</ul>-->
                <!--<div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>-->
                <!--</div>-->
                <!--</div>-->
            </div>
        </div>
    </transition>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import Vue from 'vue'
  import cartcontrol from '../cartcontrol/cartcontrol'
  import split from '../split/split'
  import ratingselect from '../ratingselect/ratingselect'
  import http from '../../assets/js/http'

  const POSITIVE = 0
  const NEGATIVE = 1
  const ALL = 2

  export default {
    props: {
      food: {
        type: Object
      }
    },
    data() {
      return {
        showFlag: false,
        selectType: ALL,
        onlyContent: true,
        desc: {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
      }
    },
    components: {
      cartcontrol,
      split,
      ratingselect
    },
    methods: {
      show() {
        this.showFlag = true
//                this.selectType = ALL;
//                this.onlyContent = true;
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.food, {
              click: true
            })
          } else {
            // 重新计算
            this.scroll.refresh()
          }
        })
      },
      hide() {
        this.showFlag = false
      },
      addFirst(event) {
        if (!event._constructed) {
          return
        }
        this.food.nums++
        let addfd = {}
        addfd['foods'] = this.food.id ? this.food.id : this.food.foods.id
        addfd['nums'] = 1
        addfd['seller'] = 2
        this.apiPost('shopcarts/', addfd).then((res) => {
          this.$nextTick(() => {
            bus.$emit('cart.add', event.target)
          })
        })
        // 分发监听给上级组件v-goods
      },
      needShow(type, text) {
        if (this.onlyContent && !text) {
          return false
        }
        if (this.selectType === ALL) {
          return true
        } else {
          return type === this.selectType
        }
      }
    },
    created() {
      // 获取子组件的selectType的更新
      // Bus.$on('ratingtype.select', selectType => {
      //   this.selectType = selectType
      //   this.$nextTick(() => {
      //     this.scroll.refresh()
      //   })
      // })

      bus.$on('content.toggle', onlyContent => {
        this.onlyContent = onlyContent
        this.$nextTick(() => {
          this.scroll.refresh()
          console.log(food)
        })
      })
    },
    filters: {
      formatDate(time) {
        let date = new Date(time)
        return formatDate(date, 'yyyy-MM-dd hh:mm')
      }
    },
    mixins: [http]
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "../../assets/css/mixins";

    .food {
        position: fixed;
        left: 0;
        top: 1rem;
        bottom: 48px;
        z-index: 30;
        width: 100%;
        background: #fff;
        &.move-enter-active, &.move-leave-active {
            transition: all .2s linear;
        }
        &.move-enter, &.move-leave-active {
            transform: translate3d(100%, 0, 0);
        }
        .food-content {
            .image-header {
                position: relative;
                width: 100%;
                height: 0;
                padding-top: 100%;
                img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
                .back {
                    position: absolute;
                    top: 10px;
                    left: 0;
                    .icon-arrow_lift {
                        display: block;
                        padding: 10px;
                        font-size: 20px;
                        color: #fff;
                    }
                }
            }
            .content {
                padding: 18px;
                position: relative;
                .title {
                    line-height: 14px;
                    margin-bottom: 8px;
                    font-size: 14px;
                    font-weight: 700;
                    color: rgb(7, 17, 27);
                }
                .detail {
                    margin-bottom: 18px;
                    line-height: 10px;
                    height: 10px;
                    font-size: 0;
                    .sell-count, .rating {
                        font-size: 10px;;
                        color: rgb(147, 153, 159);
                    }
                    .sell-count {
                        margin-right: 12px;
                    }
                }
                .price {
                    font-weight: 700;
                    line-height: 24px;
                    .now {
                        margin-right: 8px;
                        font-size: 14px;
                        color: rgb(240, 20, 20);
                    }
                    .old {
                        text-decoration: line-through;
                        font-size: 10px;
                        color: rgb(147, 153, 159);
                    }
                }
                .cartcontrol-wrapper {
                    position: absolute;
                    right: 12px;
                    bottom: 12px;
                }
                .buy {
                    position: absolute;
                    right: 18px;
                    bottom: 18px;
                    z-index: 10;
                    height: 24px;
                    line-height: 24px;
                    padding: 0 12px;
                    box-sizing: border-box;
                    border-radius: 12px;
                    font-size: 10px;
                    color: #fff;
                    background: rgb(0, 160, 220);
                    &.fade-enter-active, &.fade-leave-active {
                        transition: opacity .5s
                    }
                    &.fade-enter, &.fade-leave-active {
                        opacity: 0
                    }
                }
            }
            .info {
                padding: 18px;
                .title {
                    line-height: 14px;
                    margin-bottom: 6px;
                    font-size: 14px;
                    color: rgb(7, 17, 27);
                }
                .text {
                    line-height: 24px;
                    padding: 0 8px;
                    font-size: 12px;
                    color: rgb(77, 85, 93);
                }
            }
            .rating {
                padding-top: 18px;
                .title {
                    line-height: 14px;
                    margin-left: 18px;
                    font-size: 14px;
                    color: rgb(7, 17, 27);
                }
                .rating-wrapper {
                    padding: 0 18px;
                    .rating-item {
                        position: relative;
                        padding: 16px 0;
                        @include border-1px(rgba(7, 17, 27, .1));
                        .user {
                            position: absolute;
                            right: 0;
                            top: 16px;
                            line-height: 12px;
                            font-size: 0;
                            .name {
                                display: inline-block;
                                vertical-align: top;
                                margin-right: 6px;
                                font-size: 10px;
                                color: rgb(147, 153, 159);
                            }
                            .avatar {
                                border-radius: 50%;
                            }
                        }
                        .time {
                            margin-bottom: 6px;
                            line-height: 12px;
                            font-size: 10px;
                            color: rgb(147, 153, 159);
                        }
                        .text {
                            line-height: 16px;
                            font-size: 12px;
                            color: rgb(7, 17, 27);
                            .icon-thumb_up, .icon-thumb_down {
                                margin-right: 4px;
                                line-height: 16px;
                                font-size: 12px;
                            }
                            .icon-thumb_up {
                                color: rgb(0, 160, 220);
                            }
                            .icon-thumb_down {
                                color: rgb(147, 153, 159);
                            }
                        }
                    }
                    .no-rating {
                        padding: 16px 0;
                        font-size: 12px;
                        color: rgb(147, 153, 159);
                    }
                }
            }
        }
    }
</style>



