<template>
    <body>
    <!--header-->
    <header>
        <router-link tag="a" to="/service" class="location" id="location" data-title-type="native">
            <img src="../../../assets/img/sys.png"/>
        </router-link>

        <div class="top-sch-box flex-col logoIcon">
            <a>
                <aside class="index-searchArea">
                    <input class="input-text-searchArea" type="text" placeholder="请输入餐品"/>
                    <input type="button" value="&#63;" class="input-searchBtn"/>
                </aside>
            </a>
        </div>
        <router-link tag="a" class="rt_searchIcon" to="/service"><img style="width:70%;"
                                                                      src="../../../assets/img/mapIcon.png">
        </router-link>
    </header>
    <div class="goods">
        <div class="menu-wrapper" ref="menuWrapper">
            <ul>
                <li v-for="(item, index) in goods" class="menu-item" :class="{'current': currentIndex === index}"
                    @click="selectMenu(index, $event)">
                    <span class="text border-1px">
                        <span v-show="item.type > 0" class="icon" :class="classMap[item.type]"></span>{{ item.name }}
                    </span>
                </li>
            </ul>
        </div>
        <div class="foods-wrapper" ref="foodWrapper">
            <ul>
                <li v-for="item in goods" class="food-list food-list-hook">
                    <h1 class="title">{{ item.name }}</h1>
                    <ul>
                        <li v-for="(food,index) in item.foods" class="food-item border-1px"
                            @click="selectFood(food, $event)">
                            <div class="icon">
                                <img :src="food.icon" width="57" height="57">
                            </div>
                            <div class="content">
                                <h2 class="name">{{ food.name }}</h2>
                                <p class="desc">{{ food.description }}</p>
                                <div class="extra">
                                    <span class="count">月售{{ food.sellCount }}份</span><span>好评率{{ food.rating }}%</span>
                                </div>
                                <div class="price">
                                    <span class="now">￥{{ food.price }}</span><span v-show="food.oldPrice" class="old">￥{{ food.oldPrice }}</span>
                                </div>
                                <div class="cartcontrol-wrapper">
                                    <cartcontrol :food="food"></cartcontrol>
                                </div>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
        <shopcart ref="shopcart" :selectFoods="select_Foods" :delivery-price="2" :min-price="10"></shopcart>
        <food :food="selectedFood" ref="food"></food>
    </div>
    </body>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import shopcart from '../../shopcart/shopcart'
  import cartcontrol from '../../cartcontrol/cartcontrol'
  import food from '../../food/food'

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        goods: [],
        listHeight: [],
        scrollY: 0,
        selectedFood: {},
        username: false
      }
    },
    computed: {
      select_Foods() {
        let foods = []
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.nums > 0) {
              foods.push(food)
            }
          })
        })
        return foods
      },
      currentIndex() {
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i]
          let height2 = this.listHeight[i + 1]
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            return i
          }
        }
        return 0
      }
    },
    created() {
      let authKey = Lockr.get('token')
      if (!authKey) {
        _g.toastMsg('warning', '您还未登录，请先登录')
        setTimeout(() => {
          router.replace('/login/')
        }, 1000)
        return
      }
      this.username = Lockr.get('userInfo').username
      this.getFoods()
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
    },
    methods: {
      getFoods() {
        axios.get('my/').then((response) => {
          response = response.data
          this.goods = response
          // DOM 更新了 操作dom时一定要在$nextTick里
          this.$nextTick(() => {
            this._initScroll()
            this._calculateHeight()
          })
        })
      },
      _initScroll() {
        this.menuScroll = new BScroll(this.$refs.menuWrapper, {
          click: true
        })
        this.foodScroll = new BScroll(this.$refs.foodWrapper, {
          click: true,
          probeType: 3 // 监测实时滚动的位置
        })

        this.foodScroll.on('scroll', (pos) => {
          // 拿到实时的y坐标
          this.scrollY = Math.abs(Math.round(pos.y))
        })
      },
      _calculateHeight() {
        // 获取食物的li Dom节点列表
        let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook')
        let height = 0
        this.listHeight.push(height)
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i]
          height += item.clientHeight
          this.listHeight.push(height)
        }
      },
      selectMenu(index, event) {
        // better-scroll 可以监听到此事件，浏览器原生不能监听到  防止pc端出现两次点击
        if (!event._constructed) {
          return
        }
        // 获取食物的li Dom节点列表
        let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook')
        let el = foodList[index]
        // 调用better-scroll 方法滚动到响应位置
        this.foodScroll.scrollToElement(el, 300)
      },
      // 子组件事件触发
      drop(target) {
        // 体验优化，异步执行下落动画
        this.$nextTick(() => {
          // 父组件传递给子组件
          this.$refs.shopcart.drop(target)
        })
      },
      selectFood(food, event) {
        if (!event._constructed) {
          return
        }
        this.selectedFood = food
        this.$refs.food.show()
      }
    },
    components: {
      shopcart,
      cartcontrol,
      food
    },
    watch: {
      '$route'(to, from) {
        if (to.name != 'pay') {
          this.$router.go(0)
        }
      }
    }
  }
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
    @import "../../../assets/css/mixins";

    .goods {
        border-top: solid 0.02rem #fff;
        display: flex;
        position: absolute;
        top: 1.02rem;
        bottom: 1.2rem;
        width: 100%;
        overflow: hidden;
        .menu-wrapper {
            width: 1.6rem;
            flex: 0 0 1.6rem;
            /*background: #f3f5f7;*/
            background: #f4ea2a;
            .menu-item {
                display: table;
                height: 54px;
                width: 80px;
                line-height: 14px;
                padding: 0 12px;
                &.current {
                    position: relative;
                    margin-top: -1px;
                    z-index: 10;
                    background: #fff;
                    font-weight: 700;
                    .text {
                        @include border-none();
                    }
                }
                .icon {
                    display: inline-block;
                    vertical-align: top;
                    width: .3rem;
                    height: .3rem;
                    margin-right: .05rem;
                    background-size: .3rem .3rem;
                    background-repeat: no-repeat;
                    &.decrease {
                        @include bg-image('decrease_3');
                    }
                    &.discount {
                        @include bg-image('discount_3');
                    }
                    &.guarantee {
                        @include bg-image('guarantee_3');
                    }
                    &.invoice {
                        @include bg-image('invoice_3');
                    }
                    &.special {
                        @include bg-image('special_3');
                    }
                }
                .text {
                    display: table-cell;
                    vertical-align: middle;
                    font-size: 12px;
                    @include border-1px(#2258B4);
                }
            }
        }
        .foods-wrapper {
            flex: 1;
            .title {
                padding-left: 14px;
                height: 0.7rem;
                line-height: 0.7rem;
                /*border-left: 2px solid #2258B4;*/
                font-size: 14px;
                /*color: rgb(147, 153, 159);*/
                /*background: #f3f5f7;*/
                color: #fff;
                /*background: #f4ea2a;*/
                background: #e2bf1e;
                margin-top: 0;
            }
            .food-item {
                display: flex;
                margin: 18px;
                padding-bottom: 18px;
                @include border-1px(rgba(7, 17, 27, .1));
                &:last-child {
                    @include border-none();
                    margin-bottom: 0;
                }
                .icon {
                    flex: 0 0 57px;
                    margin-right: 10px;
                }
                .content {
                    flex: 1;
                    .name {
                        margin: 2px 0 8px;
                        line-height: 14px;
                        font-size: 14px;
                        color: rgb(7, 17, 27);
                    }
                    .desc,
                    .extra {
                        font-size: 10px;
                        color: rgb(147, 153, 159);
                    }
                    .desc {
                        line-height: 12px;
                        margin-bottom: 8px;
                    }
                    .extra {
                        line-height: 10px;
                        .count {
                            margin-right: 12px;
                        }
                    }
                    .price {
                        font-weight: 700;
                        line-height: 24px;
                        .now {
                            margin-right: 8px;
                            font-size: 14px;
                            color: rgb(240, 20, 20);
                        }
                        .old {
                            text-decoration: line-through;
                            font-size: 10px;
                            color: rgb(147, 153, 159);
                        }
                    }
                    .cartcontrol-wrapper {
                        position: absolute;
                        right: 0;
                        bottom: 12px;
                    }
                }
            }
        }
    }
</style>



